            "description": "During The Post-War Years The Experimental Tendency In British Novel Continued, But The Critics Lamented That The Mid-Twentieth Century British Literature Produced No Ulysses. William Golding S Lord Of The Flies (1954) Was Accepted As An Experiment With The Restoration Of Fable In English Fiction, A Reconstruction At An Adult Level Of R.M. Ballantyne S Coral Island. Thanks To The Nobel Prize Awarded To Golding In 1983, Lord Of The Flies Came To The Limelight And Its Underlying Philosophy Has Been Variously Interpreted Since Then. In The Face Of An Upsurge In Golding Criticism, And More Specifically, In Lord Of The Flies, The Novel Demands Fresh Discussions. The Present Book Seeks To Acquaint The Readers First With The Theme Of The Novel, And Then With The Discourses Invited By The Theme.A Detailed Analysis Of Each Of The Chapters Of The Novel Is The Special Attraction Of This Critical Study. It Is Not Just A Summary, But A Critical Analysis Of The Events, Symbols, Images And Reflections Of The Characters Introduced In The Novel. The Chapter-Wise Discussion Will Inspire The Readers To Go Through The Original Text Thoroughly For Having A First Hand Knowledge Of Golding S Art. It Will Also Help The Readers To Develop An Understanding Of Golding As A Novelist Even Before Reading The Original Text.The Chapters On Golding S Concept Of Evil And The Innate Depravity Of Man, Potential Savagery Of Children, Sin And Expiation, The Post-World War Scenario And Elements Of Post-Colonial Writing Are Designed To Project Lord Of The Flies As A Post-Colonial Novel Of Ideas. The Book Also Contains Separate Chapters On Narrative Skill, Characterization, Plot And Structure Which Are Helpful For The Students. The Discussion Is Marked By A Critical Insight That Serves Not Only The Advanced Scholars But Also The Common Students Who Will Be Benefited By The Lucid Presentation.",
