            "description": "Peter Jackson's film version of The Lord of the Rings (2001-2003) is the grandest achievement of 21st century cinema so far. But it is also linked to topical and social concerns including war, terrorism, and cultural imperialism. Its style, symbols, narrative, and structure seem always already linked to politics, cultural definition, problems of cinematic style, and the elemenal mythologies that most profoundly capture our imaginations.From Hobbits to Hollywood: Essays on Peter Jackson's Lord of the Rings treats Jackson's trilogy as having two conditions of existence: an aesthetic and a political. Like other cultural artefacts, it leads a double life asobjet d'art and public statement about the world, so that nothing in it is ever just cinematically beautiful or tasteful, and nothing is ever just a message or an opinion.Written by leading scholars in the study of cinema and cultureFrom Hobbits to Hollywood gives Jackson's trilogy the fullest scholarly interrogation to date. Ranging from interpretations ofThe Lord of the Rings' ideological and philosophical implications, through discussions of its changing fandoms and its incorporation into the Hollywood industry of stars, technology, genre, and merchandising, to considerations of CGI effects, acting, architecture and style, the essays contained here open a new vista of criticism and light, for ardent fans of J.R.R. Tolkien, followers of Jackson, and all those who yearn for a deeper appreciation of cinema and its relation to culture.",
