            "description": "\"This is the best all-around view of the Tolkien phenomenon. Thompson understands the books, she understands the movies—she also understands the money and the franchising. Best of all, she understands the people. Thompson offers cultural criticism of the highest order, examining one of the most significant shifts in contemporary popular media.\"—Tom Shippey, author of The Road to Middle-earth \"Reading these chapters has been an absolute pleasure. It’s all so complex but so succinct. Thompson has managed to do what so many others have failed to do . . . in chapter one, she has explained how all the rights to LOTR bounced around, and were finally sorted so Peter Jackson could make the movie. I’ve never understood the complexities of how that worked until now!\"—Judy Alley, Merchandising Coordinator, The Lord of the Rings \"I must say that Thompson has written the definitive study of Peter Jackson’s work in creating this remarkable production entity.\"—Alex Funke, ASC, Oscar-winning Visual Effects Director of Photography, miniatures unit, The Lord of the Rings \"I had a wonderful time reading those chapters! There’s so much I don’t know about what went on—I am in awe of all the research Thompson has done. It is an extremely interesting read! There’s so much there that I’d forgotten and I always wished there was a permanent record of many things that happened. Thompson’s account of TORN’s beginnings and how it functioned gets it absolutely right—more than that, Thompson captures how it felt to us at the time. Nobody else has managed to get enough of an understanding to do that.\"—Erica Challis (\"Tehanu\"), co-founder of TheOneRing.net",
